<template>
    <div class="cu-top">
        <el-card shadow="always">
            <div class="deta-header">
                <el-page-header @back="goBack" :content="contentName">
                </el-page-header>
            </div>
            <div style="max-width: 800px">
                <el-form label-position="left" label-width="80px" :model="formLabelAlign" ref="formLabelAlign">
                    <el-form-item label="房间名" :error="errorMassage.room_name" prop="room_name"
                                  :rules="[{required: true, message: '请输入房间名', trigger: 'blur' }]">
                        <el-input v-model="formLabelAlign.room_name"></el-input>
                    </el-form-item>
                    <el-form-item label="房间介绍" :error="errorMassage.room_introduction" prop="room_introduction"
                                  :rules="[{required: true, message: '请输入房间介绍', trigger: 'blur' }]">
                        <el-input v-model="formLabelAlign.room_introduction"></el-input>
                    </el-form-item>
                    <el-form-item label="房间容量" :error="errorMassage.room_count" prop="room_count"
                                  :rules="[{required: true, message: '请输入房间容纳人数', trigger: 'blur' }]">
                        <el-input v-model="formLabelAlign.room_count"></el-input>
                    </el-form-item>
                    <el-form-item label="房间用途" :error="errorMassage.room_use" prop="room_use"
                                  :rules="[{required: true, message: '请输入房价用途', trigger: 'blur' }]">
                        <el-input v-model="formLabelAlign.room_use"></el-input>
                    </el-form-item>
                    <el-form-item label="房间图片" :error="errorMassage.room_image" prop="room_image"
                                  :rules="[{required: true, message: '请输入房间图片', trigger: 'blur' },
                                  {type: 'url', message: '请输入图片链接', trigger: 'blur' },]">
                        <el-input v-model="formLabelAlign.room_image"></el-input>
                    </el-form-item>
                    <el-form-item style="text-align: left;" label="图片预览">
                        <el-image
                                style="max-width: 200px; max-height: 200px;min-height: 100px;min-width: 100px"
                                :src="formLabelAlign.room_image"
                                :preview-src-list="[formLabelAlign.room_image]"
                                fit="contain">
                        </el-image>
                    </el-form-item>
                </el-form>
            </div>
            <el-row :gutter="20" class="deta-row">
                <el-col :span="6">
                    <div class="" style="text-align: left">
                        <el-button icon="el-icon-arrow-left" @click="goBack">返回</el-button>
                        <el-button type="danger" icon="el-icon-delete" @click="dialogVisible = true" v-if="$route.params.id!=='add'">删除</el-button>
                    </div>
                </el-col>
                <el-col :span="6" :offset="12">
                    <div class="" style="text-align: right">
                        <el-button icon="el-icon-edit" type="primary" @click="SaveData">保存</el-button>
                    </div>
                </el-col>
            </el-row>
        </el-card>
        <!--弹出框-->
        <el-dialog
                title="警告"
                :visible.sync="dialogVisible"
                width="30%"
                @open="delData">
            <span>
                <el-tree :data="relatedData" default-expand-all></el-tree>
                <div style="font-weight: bold;color: red;font-size: 14px;margin-top: 18px">以上相关内容将被删除</div>
            </span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="optionMethods('delete'),(dialogVisible = false)">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    import base from '@/utils/baseUrl';

    export default {
        name: "RoomDetail",
        data() {
            return {
                contentName: '',
                dialogVisible: false,
                formLabelAlign: {
                    room_name: '',
                    room_introduction: '',
                    room_count: '',
                    room_use: '',
                    room_image: '',
                },
                errorMassage: {
                    room_name: '',
                    room_introduction: '',
                    room_count: '',
                    room_use: '',
                    room_image: '',
                },
                relatedData: [{
                    label:'',
                    children:[]
                }],
            }
        },
        created() {
            if(this.$route.params.id!=='add'){
                this.optionMethods('get');
            }
        },
        methods: {
            goBack() {
                this.$router.back();
            },
            SaveData() {
                if(this.$route.params.id==='add'){
                    this.formLabelAlign.college_id = this.$store.state.token.userInfo.college.id;
                    console.log(this.formLabelAlign)
                    this.optionMethods('post', this.formLabelAlign);
                }else {
                    this.optionMethods('put', this.formLabelAlign);
                }
            },
            delData() {
                this.$http({
                    method: 'get',
                    url: `${base.admin}/room/${this.$route.params.id}/getallrelated/`,
                }).then(res => {
                    console.log(res);
                    this.relatedData[0].label= res.data[0];
                    let tmpchild = ()=>{
                        let tmpdata = [];
                        for(let x of res.data[1]){
                            tmpdata.push({label:x})
                        }
                        return tmpdata
                    };
                    this.relatedData[0].children = tmpchild()
                }).catch(error => {

                });
            },
            optionMethods(method, data = null) {
                if (method === 'put' || method==='post') {
                    var resultsValid = false;
                    this.$refs['formLabelAlign'].validate((valid) => {
                            resultsValid = valid
                        }
                    );
                    if (!resultsValid) {
                        return
                    }
                }
                var url = null;
                console.log(this.$route.params);
                if(this.$route.params.id==='add'){
                   url = `${base.admin}/room/`
                }else {
                    url = `${base.admin}/room/${this.$route.params.id}/`;
                }
                this.$http({
                    method: method,
                    url: url,
                    data: data,
                }).then((res) => {
                    if (method === 'get') {
                        this.formLabelAlign = res.data;
                    } else if (method === 'put') {
                        this.$router.back();
                        this.sendMessage('修改成功！')
                    } else if (method === 'delete') {
                        this.$router.back();
                        this.sendMessage('删除成功！')
                    }else if (method === 'post') {
                        this.$router.back();
                        this.sendMessage('创建成功！')
                    }
                }).catch(error => {
                    var res = error.response;
                    if (res.status === 400) {
                        for (let [key, value] of Object.entries(res.data)) {
                            this.errorMassage[key] = value[0];
                        }
                    }
                });
            },
            sendMessage(message){
                this.$message({
                    message: message,
                    type: 'success',
                    duration:1500,
                });
            }
        },
        beforeRouteEnter(to, from, next) {
            next(vm => {
                vm.contentName = from.meta.detailname;
            })
        },
    }
</script>

<style scoped>

    .deta-header {
        margin: 10px 0 40px 0;
    }

    .deta-row {
        min-height: 90px;
        background: #f8f8f8;
        padding: 30px;
        border-radius: 4px;
        border: 1px solid #eeeeee;
    }
</style>